









































import key from '../../component/key.vue';
export default {
	computed: {
		list0() {
			return this.$store.state.expenses;
		},
		list1() {
			return this.$store.state.income;
		},
	},
	components: {
		key,
	},
	data() {
		return {
			tabIndex: 0,
			listTab: ["支出", "收入"],
			itemIndex: 0,
			listShow: {},
			showKey: true,
			record: "",
			formula: "",
			amountArr: [],
			amountStr:"",
			amount: "0.00",
		};
	},
	methods: {
		amountFun(data) {
			this.amountArr.push(data.item);
			if(data.index != "3"){
				this.amount = this.amountArr.join("");
			}
			// console.log(this.amount)
			//点击C,清除所有
			if (this.amount.includes("C")) {
				this.amount = "0.00";
				this.formula = " ";
				this.amountArr = [];
				return;
			}
			//点击.
			if (this.amount.includes(".")) {
				this.amount = Array.from(new Set(this.amountArr)).join("");
				return;
			}
			//点击+
			if (this.amount.includes("+")) {
				this.formula = this.amount;
				let add = 0;
				let amountSplitAdd = this.amount.split("+");
				for(let i=0;i<amountSplitAdd.length;i++){
					add += Number(amountSplitAdd[i]);
				}
				this.amount = add;
				return;
			}
		},
		//关闭添加，显示流水列表
		tabClose() {},
		//切换tab
		selectTab(index) {
			this.tabIndex = index;
			this.listShow = this["list" + index];
		},
		//li切换class
		selectList(item, index) {
			this.itemIndex = index;
			this.record = item;
		},
	},
	mounted() {
		this.listShow = this.list0;
		this.record = this.list0[0];
	}
}
